
"use client";

import type { Dispatch, ReactNode, SetStateAction} from 'react';
import { createContext, useState, useEffect, useCallback } from 'react';
import type { SpreadsheetData, SheetData, CellData, CellStyle } from '@/types/spreadsheet';
import { getSpreadsheet, saveSpreadsheet as dbSaveSpreadsheet } from '@/lib/db';
import { useToast } from '@/hooks/use-toast';
import { createEmptyCell, getCellId, DEFAULT_COLUMN_WIDTH } from '@/types/spreadsheet';
import { v4 as uuidv4 } from 'uuid';


const MAX_UNDO_HISTORY = 20;

interface SelectedCell {
  sheetId: string;
  rowIndex: number;
  colIndex: number;
}

interface SpreadsheetContextType {
  spreadsheet: SpreadsheetData | null;
  setSpreadsheet: Dispatch<SetStateAction<SpreadsheetData | null>>;
  isLoading: boolean;
  loadSpreadsheet: (id: string) => Promise<void>;
  saveSpreadsheet: () => Promise<void>;
  updateCell: (sheetId: string, rowIndex: number, colIndex: number, newCellData: Partial<CellData>) => void;
  selectedCell: SelectedCell | null;
  setSelectedCell: (cell: SelectedCell | null) => void;
  updateSelectedCellStyle: (styleChanges: Partial<CellStyle>) => void;
  insertRow: (sheetId: string, rowIndex: number) => void;
  deleteRow: (sheetId: string, rowIndex: number) => void;
  insertColumn: (sheetId: string, colIndex: number) => void;
  deleteColumn: (sheetId: string, colIndex: number) => void;
  appendRow: (sheetId: string) => void;
  appendColumn: (sheetId: string) => void;
  undo: () => void;
  redo: () => void;
  canUndo: boolean;
  canRedo: boolean;
}

export const SpreadsheetContext = createContext<SpreadsheetContextType | undefined>(undefined);

export function SpreadsheetProvider({ children }: { children: ReactNode }) {
  const [internalSpreadsheet, setInternalSpreadsheet] = useState<SpreadsheetData | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const { toast } = useToast();

  const [selectedCell, setSelectedCell] = useState<SelectedCell | null>(null);
  const [undoStack, setUndoStack] = useState<SpreadsheetData[]>([]);
  const [redoStack, setRedoStack] = useState<SpreadsheetData[]>([]);

  const setSpreadsheetWithHistory = useCallback((updater: SetStateAction<SpreadsheetData | null>, actionSource?: 'undo' | 'redo' | 'internal_no_history') => {
    setInternalSpreadsheet(current => {
      const newSpreadsheet = typeof updater === 'function' ? updater(current) : updater;
      
      if (actionSource !== 'undo' && actionSource !== 'redo' && actionSource !== 'internal_no_history' && current && newSpreadsheet && JSON.stringify(current) !== JSON.stringify(newSpreadsheet)) {
        const currentCopy = JSON.parse(JSON.stringify(current)) as SpreadsheetData;
        setUndoStack(prev => [...prev.slice(-MAX_UNDO_HISTORY + 1), currentCopy]);
        setRedoStack([]); 
      }
      return newSpreadsheet;
    });
  }, []);

  const exposedSetSpreadsheet = useCallback((value: SetStateAction<SpreadsheetData | null>) => {
     setSpreadsheetWithHistory(value, 'internal_no_history');
  }, [setSpreadsheetWithHistory]);


  const loadSpreadsheet = useCallback(async (id: string) => {
    setIsLoading(true);
    setSelectedCell(null);
    setUndoStack([]);
    setRedoStack([]);
    const result = await getSpreadsheet(id);
    if (result.success && result.data) {
      // Ensure columnWidths exists on loaded sheets
      const loadedSpreadsheet = result.data;
      loadedSpreadsheet.sheets = loadedSpreadsheet.sheets.map(sheet => ({
        ...sheet,
        columnWidths: sheet.columnWidths || Array(sheet.columnCount).fill(DEFAULT_COLUMN_WIDTH),
      }));
      exposedSetSpreadsheet(loadedSpreadsheet);
    } else {
      exposedSetSpreadsheet(null);
      toast({ title: "Error", description: `Failed to load spreadsheet: ${result.error || 'Not found'}.`, variant: "destructive" });
    }
    setIsLoading(false);
  }, [toast, exposedSetSpreadsheet]);

  const saveSpreadsheet = useCallback(async () => {
    if (!internalSpreadsheet) {
      toast({ title: "Error", description: "No spreadsheet data to save.", variant: "destructive" });
      return;
    }
    setIsLoading(true);
    const updatedSpreadsheet = { ...internalSpreadsheet, updatedAt: Date.now() };
    const result = await dbSaveSpreadsheet(updatedSpreadsheet);
    if (result.success) {
      exposedSetSpreadsheet(updatedSpreadsheet); 
      toast({ title: "Success", description: "Spreadsheet saved locally." });
    } else {
      toast({ title: "Error", description: `Failed to save spreadsheet: ${result.error}.`, variant: "destructive" });
    }
    setIsLoading(false);
  }, [internalSpreadsheet, toast, exposedSetSpreadsheet]);

  const updateCell = useCallback((sheetId: string, rowIndex: number, colIndex: number, newCellData: Partial<CellData>) => {
    setSpreadsheetWithHistory(prevSpreadsheet => {
      if (!prevSpreadsheet) return null;

      const newSheets = prevSpreadsheet.sheets.map(sheet => {
        if (sheet.id === sheetId) {
          const newCells = sheet.cells.map((row, rIdx) => {
            if (rIdx === rowIndex) {
              return row.map((cell, cIdx) => {
                if (cIdx === colIndex) {
                  let valueToDisplay = newCellData.rawValue;
                  let formulaString: string | undefined = cell.formula;

                  if (newCellData.rawValue !== undefined) { 
                    if (typeof newCellData.rawValue === 'string' && newCellData.rawValue.startsWith('=')) {
                      formulaString = newCellData.rawValue.substring(1);
                      valueToDisplay = newCellData.rawValue; 
                    } else {
                      formulaString = undefined; 
                    }
                  } else { 
                     valueToDisplay = cell.value;
                  }
                  
                  return { 
                    ...cell, 
                    ...newCellData, 
                    rawValue: newCellData.rawValue !== undefined ? newCellData.rawValue : cell.rawValue,
                    value: valueToDisplay, 
                    formula: formulaString 
                  };
                }
                return cell;
              });
            }
            return row;
          });
          return { ...sheet, cells: newCells };
        }
        return sheet;
      });
      return { ...prevSpreadsheet, sheets: newSheets, updatedAt: Date.now() };
    });
  }, [setSpreadsheetWithHistory]);

  const updateSelectedCellStyle = useCallback((styleChanges: Partial<CellStyle>) => {
    if (!selectedCell || !internalSpreadsheet) return;

    setSpreadsheetWithHistory(prevSpreadsheet => {
      if (!prevSpreadsheet) return null;
      const { sheetId, rowIndex, colIndex } = selectedCell;

      const newSheets = prevSpreadsheet.sheets.map(sheet => {
        if (sheet.id === sheetId) {
          const newCells = sheet.cells.map((row, rIdx) => {
            if (rIdx === rowIndex) {
              return row.map((cell, cIdx) => {
                if (cIdx === colIndex) {
                  const newStyle = { ...(cell.style || {}), ...styleChanges };
                  return { ...cell, style: newStyle };
                }
                return cell;
              });
            }
            return row;
          });
          return { ...sheet, cells: newCells };
        }
        return sheet;
      });
      return { ...prevSpreadsheet, sheets: newSheets, updatedAt: Date.now() };
    });
  }, [selectedCell, internalSpreadsheet, setSpreadsheetWithHistory]);

  const insertRow = useCallback((sheetId: string, rowIndex: number) => {
    setSpreadsheetWithHistory(prevSpreadsheet => {
      if (!prevSpreadsheet) return null;
      const newSheets = prevSpreadsheet.sheets.map(sheet => {
        if (sheet.id === sheetId) {
          const newRow: CellData[] = Array.from({ length: sheet.columnCount }, (_, cIdx) => createEmptyCell(rowIndex, cIdx));
          const updatedCells = [...sheet.cells];
          updatedCells.splice(rowIndex, 0, newRow);
          
          for (let r = rowIndex; r < updatedCells.length; r++) {
            for (let c = 0; c < updatedCells[r].length; c++) {
              updatedCells[r][c].id = getCellId(r, c);
            }
          }
          return { ...sheet, cells: updatedCells, rowCount: sheet.rowCount + 1 };
        }
        return sheet;
      });
      toast({ title: "Row Inserted", description: `Row inserted at index ${rowIndex + 1}.` });
      return { ...prevSpreadsheet, sheets: newSheets, updatedAt: Date.now() };
    });
  }, [setSpreadsheetWithHistory, toast]);

  const deleteRow = useCallback((sheetId: string, rowIndex: number) => {
    setSpreadsheetWithHistory(prevSpreadsheet => {
      if (!prevSpreadsheet) return null;
      const newSheets = prevSpreadsheet.sheets.map(sheet => {
        if (sheet.id === sheetId) {
          if (sheet.rowCount <= 1) {
            toast({ title: "Cannot Delete", description: "Spreadsheet must have at least one row.", variant: "destructive"});
            return sheet; 
          }
          const updatedCells = [...sheet.cells];
          updatedCells.splice(rowIndex, 1);

          for (let r = rowIndex; r < updatedCells.length; r++) {
            for (let c = 0; c < updatedCells[r].length; c++) {
              updatedCells[r][c].id = getCellId(r, c);
            }
          }
          return { ...sheet, cells: updatedCells, rowCount: sheet.rowCount - 1 };
        }
        return sheet;
      });
      if (prevSpreadsheet.sheets.find(s => s.id === sheetId)?.rowCount ?? 0 > 1) {
        toast({ title: "Row Deleted", description: `Row at index ${rowIndex + 1} deleted.` });
      }
      return { ...prevSpreadsheet, sheets: newSheets, updatedAt: Date.now() };
    });
  }, [setSpreadsheetWithHistory, toast]);

  const insertColumn = useCallback((sheetId: string, colIndex: number) => {
    setSpreadsheetWithHistory(prevSpreadsheet => {
      if (!prevSpreadsheet) return null;
      const newSheets = prevSpreadsheet.sheets.map(sheet => {
        if (sheet.id === sheetId) {
          const updatedCells = sheet.cells.map((row, rIdx) => {
            const newRow = [...row];
            newRow.splice(colIndex, 0, createEmptyCell(rIdx, colIndex));
            return newRow;
          });

          for (let r = 0; r < updatedCells.length; r++) {
            for (let c = colIndex; c < updatedCells[r].length; c++) {
              updatedCells[r][c].id = getCellId(r, c);
            }
          }
          const newColumnWidths = [...(sheet.columnWidths || Array(sheet.columnCount).fill(DEFAULT_COLUMN_WIDTH))];
          newColumnWidths.splice(colIndex, 0, DEFAULT_COLUMN_WIDTH);

          return { ...sheet, cells: updatedCells, columnCount: sheet.columnCount + 1, columnWidths: newColumnWidths };
        }
        return sheet;
      });
      toast({ title: "Column Inserted", description: `Column inserted at index ${getCellId(0,colIndex).replace(/[0-9]/g, '')}.` });
      return { ...prevSpreadsheet, sheets: newSheets, updatedAt: Date.now() };
    });
  }, [setSpreadsheetWithHistory, toast]);

  const deleteColumn = useCallback((sheetId: string, colIndex: number) => {
    setSpreadsheetWithHistory(prevSpreadsheet => {
      if (!prevSpreadsheet) return null;
      const newSheets = prevSpreadsheet.sheets.map(sheet => {
        if (sheet.id === sheetId) {
          if (sheet.columnCount <= 1) {
             toast({ title: "Cannot Delete", description: "Spreadsheet must have at least one column.", variant: "destructive"});
            return sheet; 
          }
          const updatedCells = sheet.cells.map(row => {
            const newRow = [...row];
            newRow.splice(colIndex, 1);
            return newRow;
          });

          for (let r = 0; r < updatedCells.length; r++) {
            for (let c = colIndex; c < updatedCells[r].length; c++) {
              updatedCells[r][c].id = getCellId(r, c);
            }
          }
          const newColumnWidths = [...(sheet.columnWidths || Array(sheet.columnCount).fill(DEFAULT_COLUMN_WIDTH))];
          newColumnWidths.splice(colIndex, 1);

          return { ...sheet, cells: updatedCells, columnCount: sheet.columnCount - 1, columnWidths: newColumnWidths };
        }
        return sheet;
      });
      if (prevSpreadsheet.sheets.find(s => s.id === sheetId)?.columnCount ?? 0 > 1) {
       toast({ title: "Column Deleted", description: `Column ${getCellId(0,colIndex).replace(/[0-9]/g, '')} deleted.` });
      }
      return { ...prevSpreadsheet, sheets: newSheets, updatedAt: Date.now() };
    });
  }, [setSpreadsheetWithHistory, toast]);

  const appendRow = useCallback((sheetId: string) => {
    setSpreadsheetWithHistory(prevSpreadsheet => {
      if (!prevSpreadsheet) return null;
      const newSheets = prevSpreadsheet.sheets.map(sheet => {
        if (sheet.id === sheetId) {
          const newRowIndex = sheet.rowCount;
          const newRow: CellData[] = Array.from({ length: sheet.columnCount }, (_, cIdx) => createEmptyCell(newRowIndex, cIdx));
          const updatedCells = [...sheet.cells, newRow];
          toast({ title: "Row Added", description: `Row added at the end.` });
          return { ...sheet, cells: updatedCells, rowCount: sheet.rowCount + 1 };
        }
        return sheet;
      });
      return { ...prevSpreadsheet, sheets: newSheets, updatedAt: Date.now() };
    });
  }, [setSpreadsheetWithHistory, toast]);

  const appendColumn = useCallback((sheetId: string) => {
    setSpreadsheetWithHistory(prevSpreadsheet => {
      if (!prevSpreadsheet) return null;
      const newSheets = prevSpreadsheet.sheets.map(sheet => {
        if (sheet.id === sheetId) {
          const newColIndex = sheet.columnCount;
          const updatedCells = sheet.cells.map((row, rIdx) => [...row, createEmptyCell(rIdx, newColIndex)]);
          const newColumnWidths = [...(sheet.columnWidths || Array(sheet.columnCount).fill(DEFAULT_COLUMN_WIDTH)), DEFAULT_COLUMN_WIDTH];
          toast({ title: "Column Added", description: `Column added at the end.` });
          return { ...sheet, cells: updatedCells, columnCount: sheet.columnCount + 1, columnWidths: newColumnWidths };
        }
        return sheet;
      });
      return { ...prevSpreadsheet, sheets: newSheets, updatedAt: Date.now() };
    });
  }, [setSpreadsheetWithHistory, toast]);

  const undo = useCallback(() => {
    if (undoStack.length > 0) {
      const previousState = undoStack[undoStack.length - 1];
      if (internalSpreadsheet) {
        const currentCopy = JSON.parse(JSON.stringify(internalSpreadsheet)) as SpreadsheetData;
        setRedoStack(prev => [currentCopy, ...prev.slice(0, MAX_UNDO_HISTORY -1)]);
      }
      setUndoStack(prev => prev.slice(0, -1));
      setSpreadsheetWithHistory(() => previousState, 'undo'); 
      setSelectedCell(null); 
      toast({ title: "Undo", description: "Last action reverted." });
    }
  }, [undoStack, internalSpreadsheet, setSpreadsheetWithHistory, toast]);

  const redo = useCallback(() => {
    if (redoStack.length > 0) {
      const nextState = redoStack[0];
       if (internalSpreadsheet) {
        const currentCopy = JSON.parse(JSON.stringify(internalSpreadsheet)) as SpreadsheetData;
        setUndoStack(prev => [...prev, currentCopy].slice(-MAX_UNDO_HISTORY));
       }
      setRedoStack(prev => prev.slice(1));
      setSpreadsheetWithHistory(() => nextState, 'redo');
      setSelectedCell(null); 
      toast({ title: "Redo", description: "Last undone action applied." });
    }
  }, [redoStack, internalSpreadsheet, setSpreadsheetWithHistory, toast]);
  
  const canUndo = undoStack.length > 0;
  const canRedo = redoStack.length > 0;

  return (
    <SpreadsheetContext.Provider value={{ 
      spreadsheet: internalSpreadsheet, 
      setSpreadsheet: exposedSetSpreadsheet,
      isLoading, 
      loadSpreadsheet, 
      saveSpreadsheet, 
      updateCell,
      selectedCell,
      setSelectedCell,
      updateSelectedCellStyle,
      insertRow,
      deleteRow,
      insertColumn,
      deleteColumn,
      appendRow,
      appendColumn,
      undo,
      redo,
      canUndo,
      canRedo
    }}>
      {children}
    </SpreadsheetContext.Provider>
  );
}

