
"use client";

import { useState, useEffect } from 'react';
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import { useSpreadsheet } from "@/hooks/useSpreadsheet";
import { getCellId } from '@/types/spreadsheet'; // Import getCellId
import { Check, XIcon, FunctionSquare } from 'lucide-react'; // Added icons

export default function FormulaBar() {
  const { spreadsheet, selectedCell, updateCell } = useSpreadsheet();
  const [currentFormula, setCurrentFormula] = useState('');
  const [isEditingFormula, setIsEditingFormula] = useState(false); // To show/hide cancel/confirm buttons

  const activeSheet = spreadsheet?.sheets.find(s => s.id === spreadsheet.activeSheetId);
  const cellData = selectedCell && activeSheet ? activeSheet.cells[selectedCell.rowIndex]?.[selectedCell.colIndex] : null;

  useEffect(() => {
    if (cellData) {
      setCurrentFormula(cellData.rawValue?.toString() || '');
    } else {
      setCurrentFormula('');
    }
    setIsEditingFormula(false); // Reset editing state when cell changes
  }, [selectedCell, cellData]);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setCurrentFormula(e.target.value);
    if (!isEditingFormula) setIsEditingFormula(true);
  };

  const submitFormula = () => {
    if (selectedCell && spreadsheet && activeSheet) {
      updateCell(activeSheet.id, selectedCell.rowIndex, selectedCell.colIndex, { rawValue: currentFormula });
    }
    setIsEditingFormula(false);
  };

  const cancelEdit = () => {
     if (cellData) {
      setCurrentFormula(cellData.rawValue?.toString() || '');
    }
    setIsEditingFormula(false);
  }

  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      submitFormula();
      // Optionally move to next cell or blur input
      (e.target as HTMLInputElement).blur();
    } else if (e.key === 'Escape') {
      e.preventDefault();
      cancelEdit();
      (e.target as HTMLInputElement).blur();
    }
  };

  const selectedCellName = selectedCell && spreadsheet
    ? getCellId(selectedCell.rowIndex, selectedCell.colIndex)
    : ' '; // Display space if no cell is selected


  return (
    <div className="p-2 border-b bg-card flex items-center gap-2 shadow-sm print:hidden">
      <Input
        type="text"
        value={selectedCellName}
        readOnly
        className="w-20 h-10 text-sm text-center font-mono bg-muted border-r-0 rounded-r-none focus-visible:ring-0 focus-visible:ring-offset-0"
        aria-label="Selected cell name"
        tabIndex={-1}
      />
      
      {isEditingFormula && (
         <>
            <Button variant="ghost" size="icon" onClick={cancelEdit} className="h-10 w-10 text-destructive" aria-label="Cancel formula edit">
                <XIcon size={18}/>
            </Button>
            <Button variant="ghost" size="icon" onClick={submitFormula} className="h-10 w-10 text-green-600" aria-label="Confirm formula edit">
                <Check size={18}/>
            </Button>
         </>
      )}
      
      <Label htmlFor="formula-input" className="font-mono text-sm p-2 bg-muted/50 rounded-l-md border h-10 flex items-center"
        onClick={() => document.getElementById('formula-input')?.focus()} // Focus input on fx click
        title="Insert Function (Not Implemented)"
      >
        <FunctionSquare size={16} />
      </Label>
      <Input
        id="formula-input"
        type="text"
        placeholder={selectedCell ? "Enter value or formula..." : "Select a cell to edit..."}
        value={currentFormula}
        onChange={handleInputChange}
        onKeyDown={handleKeyDown}
        onFocus={() => setIsEditingFormula(true)} // Show buttons on focus
        // onBlur might conflict with button clicks, handle submission/cancellation via buttons or Enter/Escape
        className="flex-grow h-10 rounded-l-none focus-visible:ring-inset"
        aria-label="Formula input bar"
        disabled={!selectedCell}
      />
    </div>
  );
}
